.current {
  padding: 0;
  margin: 0;
}
.correct {
  background: #13ff0f;
}
.incorrect {
  background: red;
}
.hide {
  display: none;
}
.pageNum {
  font-size: 12px;
  padding-top: 10px;
  font-family: sans-serif;
  color: #aaa;
}
.sectionText {
  overflow-y: hidden;
  padding: 20px 20px 0px 20px;
  font-family: 'Times New Roman', serif;
  height: 32em;
  line-height: 1.2em;
  white-space: pre-wrap;
  text-align: left;
}


.space {
  position: absolute;
  z-index: -1;
  line-height: 1.1em;
  margin-top: 1px;
}
.enter {
  position: absolute;
  line-height: 1.1em;
}
.backspace {
  color: red;
  position: absolute;
  background-color: #f5f5f5;
}

.engineContainer {
  font-size: 2.2rem;
  background-color: #f5f5f5;
  border: 1px solid #e5e5e5;
  border-radius: 3px;
}

.engineContainer span {
  white-space: pre-wrap;
}

.sectionTextBottom {
}

.noPadding {
  padding: 0 4px 4px 4px !important;
}

.grey {
  color: #444;
}
