.header {
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 15px;
  padding-right: 15px;
  background-color: #f5f5f5;
  border-bottom: 1px solid #e5e5e5;
}
.logo {
  margin-top: 0px;
  margin-bottom: 0px;
  font-size: 20px;
  text-decoration: none;
  font-size: 1.6em;
  font-family: 'Pacifico', cursive;
  color: #333;
}

.settings-link {
  color: #333;
  font-weight: bold;
  text-decoration: underline;
}

.logo:hover {
  text-decoration: none;
}

.logo:visited, .logo:active, .logo:focus {
  text-decoration: none;
  color: #333;
}
