.touchTypeHelperContainer {
  padding-top: 6px;
  height: 20em;
  background-color: #f5f5f5;
  border: 1px solid #e5e5e5;
  border-radius: 3px;
  text-align: center;
}

.key {
  border: 1px solid #999;
  border-radius: 2px;
  line-height: 0.6em;
  display: inline-block;
  padding-left: 0.1em;
  padding-top: 0.1em;
  margin-right: 1px;
  text-decoration: bold;
  font-family: sans-serif;
  font-size: 1.3em;
  position: relative;
  text-align: left;
}

.key span {
  font-size: 0.5em;
  position: absolute;
}

.highlight {
  background: #13ff0f;
}

.hands {
  position: relative;
  top: -5em;
  opacity: 0.85;
}
